.slider-item{
    background: url(./images/bg_3.jpg) no-repeat center center/cover;
    width: 100%;
    height: 81vh;
    position: relative;
    z-index: 0;
}
.slider-item1{
    background: url(./images/bg_1.jpg) no-repeat center center/cover;
    width: 100%;
    height: 81vh;
    position: relative;
    z-index: 0;
}
.slider-item2{
    position: relative;
    background: url(./images/bg_1.jpg) no-repeat center center/cover;
    width: 100%;
    height: 81vh;
    position: relative;
    z-index: 0;
}
.overlay{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: var(--main-bg);
    opacity: 0.6;
    z-index: -1;
}
@media (max-width: 1000px){
    .img-fluid{
       display: none;
    }
}