@import url(https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:ital,wght@1,300&family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
:root{
    --main-bg: black;
    --main-color: black;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    max-width: 1000px !important;
}

.slider-item{
    background: url(/static/media/bg_3.e27c6c4e.jpg) no-repeat center center/cover;
    width: 100%;
    height: 81vh;
    position: relative;
    z-index: 0;
}
.slider-item1{
    background: url(/static/media/bg_1.8134fb27.jpg) no-repeat center center/cover;
    width: 100%;
    height: 81vh;
    position: relative;
    z-index: 0;
}
.slider-item2{
    position: relative;
    background: url(/static/media/bg_1.8134fb27.jpg) no-repeat center center/cover;
    width: 100%;
    height: 81vh;
    position: relative;
    z-index: 0;
}
.overlay{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: var(--main-bg);
    opacity: 0.6;
    z-index: -1;
}
@media (max-width: 1000px){
    .img-fluid{
       display: none;
    }
}
